/* Container for the whole page */
.pageContainer {
    padding: 2rem;
    background-color: #f9f9f9;
    font-family: 'Poppins', sans-serif;
    color: #333;
    line-height: 1.6;
}

/* Hero section at the top of the page */
.hero {
    text-align: center;
    background: linear-gradient(to right, #f39c12, #e67e22);
    color: white;
    padding: 2.5rem;
    border-radius: 10px;
    margin-bottom: 2rem;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.hero h1 {
    font-size: 2.5rem;
    margin-bottom: 0.8rem;
}

.hero p {
    font-size: 1.2rem;
    margin-top: 0;
}

/* Section for the list of tours */
.tourList {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    padding: 1rem;
}

/* Individual tour cards */
.tour {
    background: #ffffff;
    border-radius: 10px;
    padding: 1.5rem;
    width: 280px;
    text-align: center;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.tour h2 {
    font-size: 1.5rem;
    color: #e67e22;
    margin-bottom: 0.5rem;
}

.tour p {
    font-size: 1rem;
    color: #7f8c8d;
    margin: 0.5rem 0;
}

/* Hover effect for tour cards */
.tour:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

/* Responsive Design */
@media (max-width: 768px) {
    .tourList {
        flex-direction: column;
        align-items: center;
    }

    .tour {
        width: 90%;
    }
}
