.pageContainer {
    padding: 2rem;
    background: #ffffff;
}

.hero {
    text-align: center;
    color: white;
    background: linear-gradient(to bottom, #ffdd57, #fbc02d);
    padding: 2rem 1rem;
    border-radius: 10px;
    margin-bottom: 2rem;
}

.bookingSection form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    max-width: 400px;
    margin: 0 auto;
}

label {
    font-weight: bold;
    color: #2d2d2d;
}

input {
    padding: 0.8rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 100%;
}

.bookButton {
    padding: 1rem;
    background: #ffdd57;
    color: #0d6efd;
    border: none;
    border-radius: 5px;
    font-weight: bold;
    cursor: pointer;
    transition: background 0.3s ease-in-out;
}

.bookButton:hover {
    background: #fbc02d;
}
