/* Container for the whole page */
.pageContainer {
    padding: 2rem;
    background-color: #f7f8fc;
    font-family: Arial, sans-serif;
    line-height: 1.6;
}

/* Hero section at the top of the page */
.hero {
    text-align: center;
    background: linear-gradient(to bottom, #3498db, #2980b9);
    color: white;
    padding: 2rem;
    border-radius: 10px;
    margin-bottom: 2rem;
}

.hero h1 {
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
}

.hero p {
    font-size: 1.2rem;
}

/* Section for the list of destinations */
.destinationList {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    max-width: 800px;
    margin: 0 auto;
}

/* Individual destination cards */
.destination {
    background: #ffffff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    padding: 1.5rem;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.destination h2 {
    font-size: 1.8rem;
    color: #2c3e50;
    margin-bottom: 0.5rem;
}

.destination p {
    font-size: 1rem;
    color: #7f8c8d;
}

/* Hover effect for destination cards */
.destination:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}
