.pageContainer {
    padding: 2rem;
    background: #f9f9f9;
}

.hero {
    text-align: center;
    background: linear-gradient(to bottom, #4CAF50, #2E8B57);
    color: white;
    padding: 2rem 1rem;
    border-radius: 10px;
    margin-bottom: 2rem;
}

.contactForm form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    max-width: 500px;
    margin: 0 auto;
}

label {
    font-weight: bold;
    color: #2d2d2d;
}

input, textarea {
    padding: 0.8rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 100%;
}

textarea {
    resize: vertical;
}

.submitButton {
    padding: 1rem;
    background: #4CAF50;
    color: white;
    border: none;
    border-radius: 5px;
    font-weight: bold;
    cursor: pointer;
    transition: background 0.3s ease-in-out;
}

.submitButton:hover {
    background: #2E8B57;
}
