/* src/styles/Navbar.module.css */

/* Base Navbar Styles */
.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #0d6efd; /* Solid blue at the top */
    padding: 20px 30px;
    font-family: 'Poppins', sans-serif;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Subtle shadow */
    transition: background-color 0.3s ease-in-out;
}

.navbar.scrolled {
    background-color: rgba(13, 110, 253, 0.8); /* Semi-transparent on scroll */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Enhanced shadow */
}

/* Logo Styles */
.logo a {
    color: #fff; /* Always white for contrast */
    font-size: 1.8rem;
    font-weight: bold;
    text-decoration: none;
    letter-spacing: 1.5px;
    transition: color 0.3s ease;
}

/* Navigation Links */
.navLinks {
    list-style: none;
    display: flex;
    gap: 30px;
    margin: 0;
    transition: transform 0.3s ease, opacity 0.3s ease, visibility 0.3s ease;
}

.navLinks li {
    margin: 0;
}

.navLinks a {
    color: #fff; /* White links */
    text-decoration: none;
    font-size: 1rem;
    padding: 10px 15px;
    font-weight: 500;
    border-radius: 5px;
    transition: color 0.3s ease, background-color 0.3s ease;
}

.navLinks a:hover {
    background-color: rgba(255, 255, 255, 0.2); /* Subtle hover effect */
    color: #ffdd57; /* Accent yellow */
}

/* Call-to-Action Button */
.ctaButton {
    background-color: #ffdd57;
    color: #0d6efd;
    padding: 10px 20px;
    font-weight: 600;
    border-radius: 25px;
    transition: background-color 0.3s ease, transform 0.2s ease;
}

.ctaButton:hover {
    background-color: #ffd700;
    transform: scale(1.1);
}

/* Responsive Hamburger Menu */
.hamburger {
    display: none;
    background: none;
    border: none;
    cursor: pointer;
    flex-direction: column;
    justify-content: space-between;
    height: 25px;
    width: 30px;
    z-index: 1100;
}

.hamburgerLine {
    height: 3px;
    width: 100%;
    background-color: #fff; /* White at all times */
    border-radius: 5px;
    transition: transform 0.3s ease, opacity 0.3s ease;
}

.navLinks.mobile {
    flex-direction: column;
    position: absolute;
    top: 70px;
    left: 0;
    width: 100%;
    background-color: rgba(13, 110, 253, 0.95); /* Mobile menu background */
    padding: 20px 0;
    border-radius: 0 0 10px 10px;
    transform: translateY(-100%);
    opacity: 0;
    visibility: hidden;
    z-index: 999;
}

.navbarOpen .navLinks.mobile {
    transform: translateY(0);
    opacity: 1;
    visibility: visible;
}

.navbarOpen .hamburgerLine:nth-child(1) {
    transform: rotate(45deg) translateY(8px);
}

.navbarOpen .hamburgerLine:nth-child(2) {
    opacity: 0;
}

.navbarOpen .hamburgerLine:nth-child(3) {
    transform: rotate(-45deg) translateY(-8px);
}

/* Responsive Styling */
@media (max-width: 768px) {
    .hamburger {
        display: flex;
    }

    .navLinks {
        display: none;
    }

    .navLinks.mobile {
        display: flex;
    }
}
