/* src/styles/HomePage.module.css */

/* General Reset */
body, h1, h2, p, ul, li, ol {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Basic Styles */
body {
    font-family: 'Poppins', sans-serif;
    color: #2d2d2d;
    background-color: #f9f9f9;
    line-height: 1.6;
}

.homepageContainer {
    width: 100%; /* Full width */
    margin: 0; /* No margin */
    padding: 0; /* No padding */
}

.hero {
    background: linear-gradient(to bottom, #4CAF50, #2E8B57);
    color: white;
    text-align: center;
    padding: 4rem 1rem;
    width: 100%; /* Full width */
}

.hero h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
}

.hero p {
    font-size: 1.2rem;
    margin-bottom: 2rem;
}

.ctaButton {
    display: inline-block;
    padding: 1rem 2rem 1rem;
    background: #fff;
    color: #4CAF50;
    border: none;
    border-radius: 5px;
    text-decoration: none;
    font-weight: bold;
    margin: 0.5rem;
    transition: all 0.3s ease-in-out;
    margin-bottom: 3rem;
}

.ctaButton:hover {
    background: #f4f4f4;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}

.bookNowButton {
    background: #ffdd57;
    color: #0d6efd;
}

.bookNowButton:hover {
    background: #68fcff;
}

.features {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    justify-content: center; /* Center aligns features */
    padding: 2rem 1rem;
    background: #e8f5e9; /* Light green background */
    width: 100%; /* Full width */
}

.feature {
    flex: 1 1 calc(25% - 2rem);
    background: white;
    border-radius: 10px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
    padding: 1.5rem;
    text-align: center;
    max-width: 300px; /* Limit feature box size */
}

.feature h2 {
    color: #4CAF50;
    margin-bottom: 1rem;
}

.feature p {
    margin-bottom: 1rem;
    color: #555;
}

.getStartedSection {
    text-align: center;
    background: #ffffff;
    padding: 2rem;
    width: 100%; /* Full width */
}

.getStartedSection h2 {
    color: #2E8B57;
}

.getStartedSection ol {
    text-align: left;
    display: inline-block;
    margin: 1rem 0;
    padding-left: 20px;
}

footer {
    background: #333;
    color: white;
    padding: 1rem;
    text-align: center;
    width: 100%; /* Full width */
}
